	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import yezhu from '@/views/yezhu/list'
	import zixun from '@/views/zixun/list'
	import weixiuxinxi from '@/views/weixiuxinxi/list'
	import users from '@/views/users/list'
	import gonggaotongzhi from '@/views/gonggaotongzhi/list'
	import baoxiu from '@/views/baoxiu/list'
	import quxiaotoupiao from '@/views/quxiaotoupiao/list'
	import wodetoupiao from '@/views/wodetoupiao/list'
	import shequhuodongbaoming from '@/views/shequhuodongbaoming/list'
	import chat from '@/views/chat/list'
	import qiandaoxinxi from '@/views/qiandaoxinxi/list'
	import shequhuodong from '@/views/shequhuodong/list'
	import toupiaoxinxi from '@/views/toupiaoxinxi/list'
	import zixunleixing from '@/views/zixunleixing/list'
	import config from '@/views/config/list'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/yezhu',
			name: '业主',
			component: yezhu
		}
		,{
			path: '/zixun',
			name: '资讯',
			component: zixun
		}
		,{
			path: '/weixiuxinxi',
			name: '维修信息',
			component: weixiuxinxi
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/gonggaotongzhi',
			name: '公告通知',
			component: gonggaotongzhi
		}
		,{
			path: '/baoxiu',
			name: '报修',
			component: baoxiu
		}
		,{
			path: '/quxiaotoupiao',
			name: '取消投票',
			component: quxiaotoupiao
		}
		,{
			path: '/wodetoupiao',
			name: '我的投票',
			component: wodetoupiao
		}
		,{
			path: '/shequhuodongbaoming',
			name: '社区活动报名',
			component: shequhuodongbaoming
		}
		,{
			path: '/chat',
			name: '客服聊天',
			component: chat
		}
		,{
			path: '/qiandaoxinxi',
			name: '签到信息',
			component: qiandaoxinxi
		}
		,{
			path: '/shequhuodong',
			name: '社区活动',
			component: shequhuodong
		}
		,{
			path: '/toupiaoxinxi',
			name: '投票信息',
			component: toupiaoxinxi
		}
		,{
			path: '/zixunleixing',
			name: '资讯类型',
			component: zixunleixing
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		]
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
